import React,{Component} from 'react';
import '../../Styles/Filters.css';

const restaurantUrl = 'https://edumatobackend.herokuapp.com/restaurant/';
const bookingUrl = 'https://edumatobackend.herokuapp.com/placeorder';

class PlaceOrder extends Component {
    constructor(props) {
        super(props);
        this.state = {
            _id: Math.floor(Math.random()*10000),
            rest_name: '',
            user_name: '',
            phone: '',
            address: '',
            persons: ''
        }
    }

    handleSubmit = () => {
        var data = {
            "_id":this.state._id,
            "rest_name":this.state.rest_name,
            "user_name":this.state.user_name,
            "phone": this.state.phone,
            "address": this.state.address,
            "persons": this.state.persons
        }

        fetch(bookingUrl,{
            method:'POST',
            headers: {
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify(data)
        })
        .then(this.props.history.push('/bookingsApi'))
    }

    handleUser = (event) => {
        this.setState({user_name:event.target.value})
    }

    handlePhone = (event) => {
        this.setState({phone:event.target.value})
    }

    handleAddress = (event) => {
        this.setState({address:event.target.value})
    }

    handlePersons = (event) => {
        this.setState({persons:event.target.value})
    }

    handleCancel = () => {
        this.props.history.push(`/restdetails/${this.props.match.params.restid}`);
    }

    render() { 
        return (
            <div className="container mt-5">
                <div className="order mb-4">Place Order</div>
                <form>
                    <div className="form-group">
                        <label htmlFor="orderid">Order ID</label>
                        <input type="text" className="form-control" id="orderid" value={this.state._id} readOnly/>
                    </div>
                    <div className="form-group">
                        <label htmlFor="restaurant">Restaurant Name</label>
                        <input type="text" className="form-control" id="restaurant" value={this.state.rest_name} readOnly/>
                    </div>
                    <div className="form-group" onChange={this.handleUser}>
                        <label htmlFor="username">User Name</label>
                        <input type="text" className="form-control" id="username"/>
                    </div>
                    <div className="form-group" onChange={this.handlePhone}>
                        <label htmlFor="phone">Phone Number</label>
                        <input type="text" className="form-control" id="phone"/>
                    </div>
                    <div className="form-group" onChange={this.handleAddress}>
                        <label htmlFor="address">Address</label>
                        <input type="text" className="form-control" id="address"/>
                    </div>
                    <div className="form-group">
                        <label htmlFor="persons">No.of persons</label>
                        <select className="form-control" id="persons" onChange={this.handlePersons}>
                            <option value="Default" selected>Choose the number of persons</option>
                            <option value="1">1</option>
                            <option value="2">2</option>
                            <option value="3">3</option>
                            <option value="4">4</option>
                            <option value="5">5</option>
                        </select>
                    </div>
                    <button className="btn btn-lg btn-warning mr-4" onClick={this.handleCancel}>Cancel</button>
                    <button className="btn btn-lg btn-primary" onClick={this.handleSubmit}>Submit</button>
                </form>
            </div>
        );
    }

    componentDidMount() {
        var restid = this.props.match.params.restid;
        fetch(`${restaurantUrl}${restid}`,{method:'GET'})
        .then(res => res.json())
        .then(data => {this.setState({rest_name:data[0].name})})
    }
}
 
export default PlaceOrder;