import React, {Component} from 'react';
import BookingDisplay from './BookingDisplay.jsx';

const BookingUrl = 'https://edumatobackend.herokuapp.com/orders';

class BookingApi extends Component {
    constructor(props) {
        super(props);
        this.state = {
            bookings: []
        }
    }
    render() { 
        return (
            <BookingDisplay bookingdata={this.state.bookings}></BookingDisplay>
        );
    }

    componentDidMount() {
        fetch(BookingUrl,{method:'GET'})
        .then(res => res.json())
        .then(data => {this.setState({bookings:data})})
    }
}
 
export default BookingApi;