import React, {Component} from 'react';
import '../../Styles/Search.css';

const locationUrl = "https://edumatobackend.herokuapp.com/location";
const restaurantUrl = "https://edumatobackend.herokuapp.com/restaurants?city=";

class Search extends Component {
    constructor() {
        super();
        this.state = {
            title:'Find the best restaurants, cafes and bars',
            locations: [],
            restaurants: []
        }
    }

    renderCity = (locations) => {
        if(locations) {
            return locations.map(location => {
                return(
                 <option value={location.city}>{location.city_name}</option>
                )
            })
        }
    }

    handleCity = (event) => {
        console.log(event.target.value);
        fetch(`${restaurantUrl}${event.target.value}`,{method:'GET'})
        .then((res) => res.json())
        .then((data) => {
            this.setState({restaurants:data})
        }) 
    }

    renderRestaurant = (restaurants) => {
        if(restaurants) {
            return restaurants.map(restaurant => {
                return(
                    <option value={restaurant._id}>{restaurant.name}</option>
                )
            })
        }
    }

    handleRestaurant = (event) => {
        this.props.rid(event.target.value);
    }

    render() { 
        //console.log(this.state.locations);
        return(
            <div className="container-fluid div1 mt-3">
                <div className="imgContainer"> 
                    <div className="logo mx-auto">
                        <p>e!</p>
                    </div>
                    <div className="caption text-center">
                        <p>{this.state.title}</p>
                    </div>
                    <div className="inputs">
                        <form className="form-inline">
                            <select onChange={this.handleCity} className="dropdown1">
                                <option value="location">Please select a location</option>
                                {this.renderCity(this.state.locations)}
                            </select>
                            <select onChange={this.handleRestaurant} className="textbox">
                                <option value="restaurant">Please select a restaurant</option>
                                {this.renderRestaurant(this.state.restaurants)}
                            </select> 
                        </form>
                    </div>
                </div>
            </div>
        )
    }

    componentDidMount() {
        fetch(locationUrl,{method:'GET'})
        .then(res => res.json())
        .then(data => this.setState({locations:data}))
    }
}
 
export default Search;