import React,{Component} from 'react';
import '../../../Styles/Filters.css';

const url = "https://edumatobackend.herokuapp.com/restaurants/";

class CuisineFilter extends Component {
    constructor() {
        super();
    }

    cuisinesort = (event) => {
        let mealtype = sessionStorage.getItem('type');
        var cuisineid  = event.target.value;
        console.log(cuisineid);
        fetch(`${url}${mealtype}?cuisine=${cuisineid}`)
        .then(res => res.json())
        .then(data => this.props.filterforcuisine(data))   
    }

    render() {
        return(
            <div onChange={this.cuisinesort}>
                <div className="cuisine1 mt-4">Cuisine</div>
                <div className="custom-control custom-radio mt-2">
                    <input className="custom-control-input" id="customChk1" type="radio" value="1"/>
                    <label className="cuisineCost custom-control-label" htmlFor="customChk1">North Indian</label>
                </div> 
                <div className="custom-control custom-radio mt-2">
                    <input className="custom-control-input" id="customChk2" type="radio" value="2"/>
                    <label className="cuisineCost custom-control-label" htmlFor="customChk2">South Indian</label>
                </div>
                <div className="custom-control custom-radio mt-2">
                    <input className="custom-control-input" id="customChk3" type="radio" value="3"/>
                    <label className="cuisineCost custom-control-label" htmlFor="customChk3">Chinese</label>
                </div>
                <div className="custom-control custom-radio mt-2">
                    <input className="custom-control-input" id="customChk4" type="radio" value="4"/>
                    <label className="cuisineCost custom-control-label" htmlFor="customChk4">Fast Food</label>
                </div>
                <div className="custom-control custom-radio mt-2">
                    <input className="custom-control-input" id="customChk5" type="radio" value="5"/>
                    <label className="cuisineCost custom-control-label" htmlFor="customChk5">Street Food</label>
                </div>
            </div>
        )
    }
}

export default CuisineFilter;