import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import GalleryCarousel from './GalleryCarousel.jsx';
import 'react-tabs/style/react-tabs.css';
import '../../Styles/DetailsPage.css';

const DetailsUrl = "https://edumatobackend.herokuapp.com/restaurant";

class RestaurantDetails extends Component {
    constructor(props) {
        super(props);
        this.state = {
            restdetails:{
                "Cuisine": [
                    {"name":""},
                    {"name":""}
                ]
            }
        }
    }

    onBack = () => {
        let mealtype = sessionStorage.getItem('type');
        this.props.history.push(`/restlist/${mealtype}`)
    }

    render() { 
        var {restdetails} = this.state;

        return (
            <div className="bodyContainer">
                <div className="container">
                    <div>
                        <img src={restdetails.thumb} alt="" className="imageContainer"/>
                        <div className="gallery">
                            <a href="#" style={{textDecoration: 'none', color: '#192f60'}} data-toggle="modal" data-target="#galleryModal">Click to see Image Gallery</a>
                            <div class="modal fade" id="galleryModal">
                                <div class="modal-dialog modal-xl modal-dialog-centered">
                                    <div class="modal-content">
                                        <div class="modal-header">
                                            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                                <span aria-hidden="true">&times;</span>
                                            </button>
                                        </div>
                                        <div class="modal-body">
                                            <GalleryCarousel></GalleryCarousel>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="restaurantName">
                        <p>{restdetails.name}</p>
                    </div>

                    <Tabs>
                        <TabList>
                            <Tab>Overview</Tab>
                            <Tab>Contact</Tab>
                        </TabList>
                        
                        <TabPanel>
                            <div className="about">About this place</div>
                            <div className="cuisine">Cuisine</div>
                            <div className="cuisineType">{restdetails.Cuisine[0].name}, {restdetails.Cuisine[1].name}</div>
                            <div className="averageCost">Average Cost</div>
                            <div className="costfortwo">â‚¹{restdetails.cost} for two people (approx.)</div>
                        </TabPanel>

                        <TabPanel>
                            <div className="phoneNumber">
                                <p>Phone Number</p>
                            </div>
                            <div className="number">
                                +91 114004566
                            </div>
                            <div>
                                <p className="name">{restdetails.name}</p>
                                <p className="address1">{restdetails.address}</p>
                            </div>
                        </TabPanel>
                    </Tabs>
                    <div className="mt-5">
                        <button type="button" className="btn btn-lg btn-danger backbtn" onClick={this.onBack}>Back</button>
                        <Link to={`/placeorder/${restdetails._id}`}>
                            <button type="button" className="btn btn-lg btn-success ml-4">Place Order</button>
                        </Link>
                    </div>
                </div>
            </div>
        )
    }

    componentDidMount() {
        var restid = this.props.match.params.id;
        fetch(`${DetailsUrl}/${restid}`,{method:'GET'})
        .then(res => res.json())
        .then(data => this.setState({restdetails:data[0]}))
    }
}
 
export default RestaurantDetails;