import React, { Component } from 'react';
import '../../../Styles/Filters.css';

const url = 'https://edumatobackend.herokuapp.com/restaurants/'

class CostFilter extends Component {
    constructor() {
        super();
    }

    costLogic = (event) => {
        let mealtype = sessionStorage.getItem('type');
        let cost = (event.target.value).split(',');
        let lcost = cost[0];
        let hcost = cost[1];

        const costUrl = `${url}${mealtype}?lcost=${lcost}&hcost=${hcost}`;
        console.log(costUrl);

        fetch(costUrl,{method:'GET'})
        .then(res => res.json())
        .then(data => this.props.datapercost(data))
    }

    render() { 
        return (
            <div onChange={this.costLogic}>
                <div className="costfortwo1 mt-4">Cost for Two</div>
                <div className="custom-control custom-radio mt-3">
                    <input className="custom-control-input" id="customRd1" type="radio" name="costFilter" value="0,500"/>
                    <label className="cuisineCost custom-control-label" htmlFor="customRd1">Less than Rs.500</label>
                </div>
                <div className="custom-control custom-radio mt-2">
                    <input className="custom-control-input" id="customRd2" type="radio" name="costFilter" value="501,1000"/>
                    <label className="cuisineCost custom-control-label" htmlFor="customRd2">Rs.500 to Rs.1000</label>
                </div>
                <div className="custom-control custom-radio mt-2">
                    <input className="custom-control-input" id="customRd3" type="radio" name="costFilter" value="1001,1500"/>
                    <label className="cuisineCost custom-control-label" htmlFor="customRd3">Rs.1000 to Rs.1500</label>
                </div>
                <div className="custom-control custom-radio mt-2">
                    <input className="custom-control-input" id="customRd4" type="radio" name="costFilter" value="1501,2000"/>
                    <label className="cuisineCost custom-control-label" htmlFor="customRd4">Rs.1500 to Rs.2000</label>
                </div>
                <div className="custom-control custom-radio mt-2">
                    <input className="custom-control-input" id="customRd5" type="radio" name="costFilter" value="2001"/>
                    <label className="cuisineCost custom-control-label" htmlFor="customRd5">Rs.2000+</label>
                </div>
            </div>
        );
    }
}
 
export default CostFilter;