import React from 'react';
import {BrowserRouter,Route} from 'react-router-dom';
import Header from './Component/Header.jsx';
import HomePage from './Component/Homepage/Homepage.jsx';
import ListingPageApi from './Component/ListingPage/ListingPageApi.jsx';
import RestaurantDetails from './Component/DetailsPage/RestaurantDetails.jsx';
import PlaceOrder from './Component/Orders/PlaceOrder.jsx';
import BookingApi from './Component/Orders/BookingApi.jsx';

const Routing = () => {
  return(
    <BrowserRouter>
      <Header></Header>
      <Route exact path='/' component={HomePage}></Route>
      <Route path='/restlist/:mealtypeid' component={ListingPageApi}></Route>
      <Route path='/restdetails/:id' component={RestaurantDetails}></Route>
      <Route path='/placeorder/:restid' component={PlaceOrder}></Route>
      <Route path='/bookingsApi' component={BookingApi}></Route>
    </BrowserRouter>
  )
}

export default Routing;