import React from 'react';
import {Link} from 'react-router-dom';
import '../Styles/Header.css';

const Header = () => {
    return(
        <div className="header d-flex">
            <div className="logo1 mr-auto"><Link to="/" style={{textDecoration: 'none', color: '#ce0505'}}>e!</Link></div>
            <div className="login mr-4"><Link to="/bookingsApi" style={{textDecoration: 'none', color: 'white'}}>Bookings</Link></div>
            <div className="login"><a href="#" style={{textDecoration: 'none', color: 'white'}}>Login</a></div>
            <div className="createAccount"><a href="#" style={{textDecoration: 'none', color: 'white'}}>Create an account</a></div>
        </div>
    )
}

export default Header;
    
