import React, {Component} from 'react';
import ListingPageDisplay from './ListingPageDisplay.jsx';
import CityFilter from './Filters/CityFilter.jsx';
import CuisineFilter from './Filters/CuisineFilter.jsx';
import CostFilter from './Filters/CostFilter.jsx';
import CitySort from './Filters/CostSort.jsx';
import '../../Styles/Filters.css';

const ListingUrl = 'https://edumatobackend.herokuapp.com/restaurants?mealtype=';
const mealUrl = 'https://edumatobackend.herokuapp.com/mealtype/';
const limit = 2;

class ListingPageApi extends Component {
    constructor() {
        super();
        this.state = {
            restaurantList: [],
            mealname: ''
        }
    }
 
    handleCost = (data) => {
        this.setState({restaurantList:data})
    }

    handleSortprice = (data) => {
        this.setState({restaurantList:data})
    }

    handleCity = (data) => {
        this.setState({restaurantList:data}) 
    }

    handleCuisine = (data) => {
        this.setState({restaurantList:data})
    }

    handlePlaces = (mealid) => {
        fetch(`${mealUrl}${mealid}`,{method:'GET'})
        .then(res => res.json())
        .then(data => {this.setState({mealname:data[0].name})})
    }
    
    render() { 
        return (
            <React.Fragment>
                <div className="heading">{this.handlePlaces(this.props.match.params.mealtypeid)}{this.state.mealname} Places</div>
                <button type="button" className="btn btn-white btn-lg shadow filterbutton" data-toggle="collapse" data-target="#filterCollapse" aria-expanded="true">Filter/Sort</button>
                <div className="row">
                    <div className="col-sm-8 col-md-4 col-lg-4 col-xl-2 filter mt-4 collapse show shadow" id="filterCollapse">
                        <div className="filterHeading mt-3 ml-4">Filters</div>
                        <CityFilter datapercity={data => {this.handleCity(data)}}></CityFilter>
                        <div className="filterSort">
                            <CuisineFilter filterforcuisine={data => {this.handleCuisine(data)}}></CuisineFilter>
                            <CostFilter datapercost={data => {this.handleCost(data)}}></CostFilter>
                            <CitySort sortprice={data => {this.handleSortprice(data)}}></CitySort>
                        </div>
                    </div>
                    <div className="col-sm-12 col-md-10 col-lg-8 col-xl-6">
                        <ListingPageDisplay restdata={this.state.restaurantList}></ListingPageDisplay> 
                    </div>
                </div>
            </React.Fragment>
        );
    }

    componentDidMount() {
        var mealType = this.props.match.params.mealtypeid;
        sessionStorage.setItem('type',mealType);
        fetch(`${ListingUrl}${mealType}`,{method:'GET'})
        .then(res => res.json())
        .then(data => this.setState({restaurantList:data}
        ))
    }
}
 
export default ListingPageApi;