import React, {Component} from 'react';
import QuickSearchDisplay from './QuickSearchDisplay.jsx';

const mealtypeUrl = 'https://edumatobackend.herokuapp.com/mealtype';

class QuickSearchApi extends Component {
    constructor() {
        super();
        this.state = {
            mealtype: []
        }
    }

    render() { 
        return(
            <QuickSearchDisplay mealdata={this.state.mealtype}></QuickSearchDisplay>
        );
    }

    componentDidMount() {
        fetch(mealtypeUrl,{method:'GET'})
        .then(res => res.json())
        .then(data => this.setState({mealtype:data}))
    }
}
 
export default QuickSearchApi;