import React from 'react';
import '../../Styles/Filters.css';

const BookingDisplay = (props) => {
    const renderBooking = ({bookingdata}) => {
        if(bookingdata) {
            return(bookingdata.map(data => {
                return(
                    <tr>
                        <td>{data._id}</td>
                        <td>{data.rest_name}</td>
                        <td>{data.user_name}</td>
                        <td>{data.phone}</td>
                        <td>{data.address}</td>
                        <td>{data.persons}</td>
                    </tr>
                )
            }))
        }
    }

    return (
        <div className="container mx-auto mt-5">
            <h3 className="mb-3 order">Orders</h3>
            <table className="table table-striped">
                <thead className="thead-dark">
                    <tr>
                        <th>Order ID</th>
                        <th>Restaurant Name</th>
                        <th>User Name</th>
                        <th>Phone Number</th>
                        <th>Address</th>
                        <th>No. of persons</th>
                    </tr>
                </thead>
                <tbody className="table-hover">
                    {renderBooking(props)}
                </tbody>
            </table>
        </div>
    );
}
 
export default BookingDisplay;