import React from 'react';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import {Carousel} from 'react-responsive-carousel';
import img1 from '../../Images/1B167734-C2DA-4476-A73C-341BC905AC5F.png';
import img2 from '../../Images/C809C815-37EA-48FB-B898-E85D21D6BCEF.png';
import img3 from '../../Images/breakfast.jpg';

const GalleryCarousel = () => {
    return (
        <Carousel>
            <div>
                <img src={img1} className="d-block w-75 rounded mx-auto" alt="breakfast1" />
            </div>
            <div>
                <img src={img2} className="d-block w-75 rounded mx-auto" alt="breakfast2" />
            </div>
            <div>
                <img src={img3} className="d-block w-75 rounded mx-auto" alt="breakfast3" />
            </div>
        </Carousel>
    );
}
 
export default GalleryCarousel;