import React from 'react';
import {Link} from 'react-router-dom';
import '../../Styles/Filters.css';

const ListingPageDisplay = (props) => {
    const renderRestaurant = ({restdata}) => {
        if(restdata) {
            if(restdata.length > 0) {
                return restdata.map((restaurant) => {
                    return(
                        <div className="hotel1 mt-4 ml-sm-5 shadow">
                            <div className="row">
                                <img className="img1 ml-5 mt-4 float-left" src={restaurant.thumb}/>
                                <div className="tileText1">
                                    <Link to={`/restdetails/${restaurant._id}`}>
                                        <div className="tileHeading1">
                                            {restaurant.name}
                                        </div>
                                    </Link>
                                    <div className="tileSubHeading">
                                        <div className="fort mb-1">{restaurant.locality}</div>
                                        <div>{restaurant.address}</div>
                                    </div>  
                                </div>
                            </div>
                            <hr/>
                            <div className="ml-5">
                                <span className="cuisines2 mr-5">CUISINES:</span>
                                <span className="ml-5 cuisines3">{restaurant.Cuisine[0].name}, {restaurant.Cuisine[1].name}</span>
                            </div>
                            <div className="mt-3 ml-5">
                                <span className="cuisines2">COST FOR TWO:</span>
                                <span className="ml-5 cuisines3">Rs. {restaurant.cost}</span>
                            </div>
                        </div> 
                    )
                })
            }

            else {
                return(
                    <div className="hotel1 tileHeading1 col-md-10 shadow">
                        <center>No data found</center>
                    </div>
                )
            }
        }

        else {
            return(
                <div>
                    <img src="/images/loader.gif"/>
                </div>
            )
        }
    }
    return (
        renderRestaurant(props)
    );
}
 
export default ListingPageDisplay;