import React from 'react';
import '../../Styles/QuickSearchDisplay.css';
import {Link} from 'react-router-dom';

const QuickSearchDisplay = (props) => {
    const renderMealtype = ({mealdata}) => {
        if(mealdata) {
            return mealdata.map(meal => {
                return(
                    <div className="shadow col-xl-3 col-lg-5 col-md-7 col-sm-7 mt-lg-3 mr-xl-5 mr-lg-5 mb-4">
                        <img src={`./images/${meal.name}.png`} alt={meal.name} className="imageThumbnail"/>
                        <div className="tileText">
                            <Link to={`/restlist/${meal.mealtype}`} style={{'textDecoration':'none'}}>
                                <div className="tileHeading">
                                    {meal.name}
                                </div>
                            </Link>
                            <div className="tileSubHeading1">
                                Start your day with exclusive {meal.name}
                            </div>
                        </div>
                    </div>          
                )
            })
        }
        
    }

    return(
        <div className="container-fluid div1">
            <div className="mealType">
                <p className="quickSearch">Quick Searches</p>
                <p className="meal">Discover restaurant by type of meal</p>
            </div>
            <div className="initial row no-gutters">
                {renderMealtype(props)}
            </div>
        </div> 
    )
}
 
export default QuickSearchDisplay;