import React from 'react';
import Search from './Search.jsx';
import QuickSearchApi from './QuickSearchApi.jsx';

const Homepage = (props) => {

    const handleRest = (data) => {
        props.history.push(`/restdetails/${data}`)
    }

    return (
        <React.Fragment>
            <Search rid={(data) => handleRest(data)}></Search>
            <QuickSearchApi></QuickSearchApi>
        </React.Fragment>
    );
}
 
export default Homepage;